import Greensock from '../views/index'
import Preloader from '../components/Preloader'
import Castle from '../components/Castle'
import KineticJS from '../components/KineticJS'
import shareRoute from '../../../utils/shareRoute'

export default [
  {
    path: '/greensock',
    component: Greensock,
    children: [
      {
        path: 'preloader',
        name: 'preloader',
        component: Preloader
      },
      {
        path: 'castle',
        name: 'castle',
        component: Castle
      },
      {
        path: 'kineticjs',
        name: 'kineticjs',
        component: KineticJS
      },
    ]
  },
  shareRoute
];



